<template>
	<view style="background-color: #f8f8f8;">
		<!-- 餐品名称价格特色轮播 -->
		<view class="detailStyle" v-for="item in detailData" :key="item.name">
			<!-- 名字 -->
			<text class="item-menu-name">{{item.name}}</text>
			<!-- 详情页轮播 -->
			<view class="wrap"   >
				<u-swiper :list="item.foods_banner_imgs" :height="450" ></u-swiper>
			</view>
			<text class="u-line-2 u-p-l-20 u-p-r-20 u-m-t-20">{{item.foods_desc}}</text>
			<text class="total-price">￥{{ item.price }}</text>
			<u-divider></u-divider>
		</view>
		<!-- 评价 -->
		<view class="tag">
			<view style="font-size: 32rpx;">食客评价</view>
			<u-tag text="味道不错" mode="light" type="info" />
			<u-tag text="价格公道" mode="light" type="info"/>
			<u-tag text="好评+1" mode="light" type="info"/>
			<u-tag text="服务态度好" mode="light" type="info"/>
		</view>
		<view class="comment" v-for="(res, index) in commentList" :key="res.id">
			<view class="left">
				<image :src="res.url" mode="aspectFill"></image>
			</view>
			<view class="right">
				<view class="top">
					<view class="name">{{ res.name }}</view>
					<view class="like" :class="{ highlight: res.isLike }">
						<view class="num">{{ res.likeNum }}</view>
						<u-icon v-if="!res.isLike" name="thumb-up" :size="30" color="#9a9a9a" @click="getLike(index)">
						</u-icon>
						<u-icon v-if="res.isLike" name="thumb-up-fill" :size="30" @click="getLike(index)"></u-icon>
					</view>
				</view>
				<view class="content">{{ res.contentText }}</view>
			</view>
		</view>
		<!-- 底部操作菜单 -->
		<view class="page-bottom">
			<navigator url="/pages/index/index" open-type="switchTab" class="p-b-btn">
				<u-icon name="home" :size="40" :color="$u.color['contentColor']"></u-icon>
				<text>首页</text>
			</navigator>
			<navigator url="/pages/cart/cart" open-type="switchTab" class="p-b-btn">
				<u-icon name="shopping-cart" :size="40" :color="$u.color['contentColor']"></u-icon>
				<text>购物车</text>
			</navigator>
			<view class="p-b-btn" :class="{active: favorite}" @click="toFavorite">
				<u-icon name="heart" :size="40" :color="$u.color['contentColor']"></u-icon>
				<text>收藏</text>
			</view>
			
			<view class="action-btn-group">
				<button type="primary" class=" action-btn no-border buy-now-btn" @click="buy">立即购买</button>
				<button type="primary" class=" action-btn no-border add-cart-btn">加入购物车</button>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				detailData: [], // 存储详情页数据
				commentList: [] // 评论信息
			}
		},
		async onLoad(option) {
			this.getComment();
			// 获取菜单页传递的菜品名称,连接数据库查询数据渲染
			const db = uniCloud.database();
			const collection = db.collection('dc-goods')
			// 根据菜品名查询数据
			const res = await collection.where({
				'dc_foods.foods.name': option.name
			}).limit(1).get()
			// 处理查询的数据 获取指定内容
			const filterData = res.result.data[0].dc_foods.foods
			this.detailData = filterData.filter(res=> {
				// 过滤数据
				return res.name == option.name
			})
			console.log(this.detailData);
		},
		methods: {
			// 评论点赞
			getLike(index) {
				this.commentList[index].isLike = !this.commentList[index].isLike;
				if (this.commentList[index].isLike == true) {
					this.commentList[index].likeNum++;
				} else {
					this.commentList[index].likeNum--;
				}
			},
			// 评论列表
			getComment() {
				this.commentList = [{
						id: 1,
						name: '叶轻眉',
						date: '12-25 18:58',
						contentText: '味道挺不错的,朋友都很喜欢吃,说下次还要继续点。 今天还是比较幸运的,去了不用排队,可能我们去比较早吧。 虽然中间出了一点小插曲,但还好不影响我们吃,而且店家提供的量是够的。 还担心不好吃呢,结果是感觉比想象中令人满意,..',
						url: 'https://cdn.uviewui.com/uview/template/SmilingDog.jpg',
						allReply: 12,
						likeNum: 33,
						isLike: false,
					},
					{
						id: 2,
						name: '叶轻眉1',
						date: '01-25 13:58',
						contentText: '味道超赞，而且一看就让人觉得很有食欲，但是分量不大，属于比较精致的类型。胜在菜品也很新鲜，味道咸淡适中，而且火候掌握的刚好。',
						allReply: 0,
						likeNum: 11,
						isLike: false,
						url: 'https://cdn.uviewui.com/uview/template/niannian.jpg',
					},
					{
						id: 3,
						name: '叶轻眉2',
						date: '03-25 13:58',
						contentText: '这家餐厅环境很好，非常干净整洁，食物不仅外形美观，重要的是味道非常棒，色味俱佳。做法保留了食材原有的味道，清新爽口。',
						allReply: 0,
						likeNum: 21,
						isLike: false,
						allReply: 2,
						url: '../../../static/logo.png',
					},
					{
						id: 4,
						name: '叶轻眉3',
						date: '06-20 13:58',
						contentText: '我不信伊朗会没有后续反应，美国肯定会为今天的事情付出代价的',
						url: 'https://cdn.uviewui.com/uview/template/SmilingDog.jpg',
						allReply: 0,
						likeNum: 150,
						isLike: false
					}
				];
			}
		}
	};
</script>

<style lang="scss" scoped>
	.detailStyle {
		margin: 0 8rpx 10rpx 8rpx;
		padding: 10rpx 10rpx;
		// background-color: #fafafa;
		border: 1px solid #f5f5f5;
		border-radius: 20rpx;
		box-shadow: 0 0 1px #7c7c7c;
	}
	// 详细信息
	.item-menu-name {
		font-weight: normal;
		font-size: 35rpx;
		margin: 6rpx 25rpx;
		color: $u-main-color;
	}
	.total-price {
		color: red;
	}
	
	.tag {
		padding-left: 30rpx; 
		.u-tag {
			margin: 20rpx 15rpx;
		}
	}
	.wrap {
		padding: 20rpx 20rpx;
	}
	.comment {
		display: flex;
		padding: 30rpx;
		.left {
			image {
				width: 64rpx;
				height: 64rpx;
				border-radius: 50%;
				background-color: #f2f2f2;
			}
		}

		.right {
			flex: 1;
			padding-left: 20rpx;
			font-size: 30rpx;

			.top {
				display: flex;
				justify-content: space-between;
				align-items: center;
				margin-bottom: 10rpx;

				.name {
					color: #5677fc;
				}

				.like {
					display: flex;
					align-items: center;
					color: #9a9a9a;
					font-size: 26rpx;

					.num {
						margin-right: 4rpx;
						color: #9a9a9a;
					}
				}

				.highlight {
					color: #5677fc;

					.num {
						color: #5677fc;
					}
				}
			}

			.content {
				margin-bottom: 10rpx;
			}
		}
	}
	
	/* 底部操作菜单 */
		.page-bottom{
			position:fixed;
			left: 30upx;
			bottom:30upx;
			z-index: 95;
			display: flex;
			justify-content: center;
			align-items: center;
			width: 690upx;
			height: 100upx;
			background: rgba(255,255,255,.9);
			box-shadow: 0 0 20upx 0 rgba(0,0,0,.5);
			border-radius: 16upx;
			
			.p-b-btn{
				display:flex;
				flex-direction: column;
				align-items: center;
				justify-content: center;
				font-size: 24rpx;
				color: #000000;
				width: 96upx;
				height: 80upx;
				&.active, &.active .yticon{
					color: #000000;
				}
			}
			.action-btn-group{
				display: flex;
				height: 76upx;
				border-radius: 100px;
				overflow: hidden;
				box-shadow: 0 20upx 40upx -16upx #fa436a;
				box-shadow: 1px 2px 5px rgba(219, 63, 96, 0.4);
				background: linear-gradient(to right, #ffac30,#fa436a,#F56C6C);
				margin-left: 20upx;
				position:relative;
				&:after{
					content: '';
					position:absolute;
					top: 50%;
					right: 50%;
					transform: translateY(-50%);
					height: 28upx;
					width: 0;
					border-right: 1px solid rgba(255,255,255,.5);
				}
				.action-btn{
					display:flex;
					align-items: center;
					justify-content: center;
					width: 180upx;
					height: 100%;
					font-size: 24rpx ;
					padding: 0;
					border-radius: 0;
					background: transparent;
				}
			}
		}
</style>
